@import url('https://fonts.googleapis.com/css2?family=Nanum+Gothic:wght@700&display=swap');
.menu {
    margin-right: 10px;
    margin-top: 4px;
    width: 30%;
    border: 3px solid white;
    border-radius: 10px;
}

.showBurger {
    margin-right: 7px;
    margin-top: 3px;
    margin-bottom: 5px;
    margin-left: 7px;
    width: 30%;
    padding-top: 3px;
    border: 3px solid white;
    border-radius: 10px;
    background-color: white;
    float: left;
}

.showBurger:hover {
    border: 3px solid gray;
}

.resultBurger {
    margin-right: 7px;
    margin-top: 3px;
    margin-bottom: 5px;
    margin-left: 7px;
    width: 30%;
    padding-top: 3px;
    border-radius: 10px;
}

.image {
    width: 80%;
}

#menuShower {
    content: '';
    display: block;
    clear: both;
    float: left;
    width: 100%;
}

.dialog2_short2 {
    position: relative;
    font-size: 18px;
    margin-left: 10px;
    margin-right: 10px;
    margin-bottom: 10px;
    margin-top: 5px;
    width: 380px;
    height: 40px;
    float: left;
    padding: 10px;
    background: #FFF8D6;
    border-radius: 10px 10px 10px 3px;
}

#selected {
    width: 30%;
    margin-top: 10px;
    margin-bottom: 5px;
    margin-right: 100px;
    height: 40px;
    font-size: 20px;
    background-color: salmon;
    border-radius: 10px 10px 10px 10px;
    font-family: 'Nanum Gothic', sans-serif;
}